/* _reset.scss */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  line-height: 1.5;
  font-family: sans-serif;
  background-color: #fff;
  color: #000;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

/* base/_typography.scss */
body {
  font-family: sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #333;
}

/* layout/_header.scss */
header {
  background-color: #f8f8f8;
  padding: 1rem;
}

/* layout/_footer.scss */
footer {
  background-color: #f1f1f1;
  padding: 1rem;
  text-align: center;
}

/* components/_buttons.scss */
button {
  padding: 0.5rem 1rem;
  background-color: #0073e6;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.headline-section {
  padding: 2rem 1rem;
}
.headline-section .above-headline,
.headline-section .below-headline {
  font-size: 0.875rem;
  color: #666;
  margin-bottom: 0.5rem;
}
.headline-section .headline {
  font-weight: bold;
  margin: 0.5rem 0;
}
.headline-section .headline-paragraph {
  margin-top: 1rem;
  font-size: 1rem;
  color: #333;
}
.headline-section.text-center {
  text-align: center;
}
.headline-section.text-right {
  text-align: right;
}
.headline-section.text-left {
  text-align: left;
}